/* Fonts */

@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');



html, body {
    overscroll-behavior: none;
    margin-top: 10vh;
    font-family: 'Inter';
}


/* Navbar */

@keyframes shrink-nav {
  from {
    height: 20vh;
  }
  to {
    height: 10vh;
  }
}

@keyframes scale-content {
  to {
    transform: scale(0.7);
  }
}

@keyframes disappear {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.bar {
    width: 100vw;
    top: 0;
    position: fixed;
    height: 20vh;
    background-color: rgba(255, 255, 255, 1);
    box-shadow: 1vh 1vh 5vh rgba(255,255,255,0.4);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2vw;
    overflow-y: none;
    z-index: 10;
    backdrop-filter: blur(1vh);

    /* Link animation to scroll */
    animation: shrink-nav linear both;
    animation-timeline: scroll();
    animation-range: 0px 150px; /* Starts at 0px, finishes at 150px scroll */
}

h1 {
    font-family: "Inter";
    font-weight: bold;
    font-size: 7.5vh;
    color: white;
    /* text-shadow: 2px 2px 10px #000000; */
    padding: 0;
    margin: 0;
    /* width: 10vh; */
    height: 100%;
    /* text-align: center; */
    user-select: none;
    /* vertical-align: middle; */
    line-height: 8.5vh;
}

.blackTile {
    background-color: black;
    width: 10vh;
    height: 10vh;
    border-radius: 2vh;

    animation: scale-content linear both;
  animation-timeline: scroll();
  animation-range: 0px 150px;
}

.tile {
    width: 10vh;
    height: 10vh;
    border-radius: 2vh;
    display: flex;
    align-items: center;
    justify-content: center;

    animation: scale-content linear both;
  animation-timeline: scroll();
  animation-range: 0px 150px;
}

.navbarItems {
    color: black;
    font-size: 3vh;
    font-family: "Inter";

    /* Link animation to scroll */
    animation: disappear linear both;
    animation-timeline: scroll();
    animation-range: 0px 150px; /* Starts at 0px, finishes at 150px scroll */
}


/* Photo */

.carols {
    width: 100vw;
    object-fit: cover;
    height: 80vh;
    object-position: 50% 0%;
}

