/* Inkless */

.inkless_section {
    margin-top: 10vh;
    width: 80vw;
    margin-left: 10vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    gap: 7vh;
}

.leftFlex {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 2vh;
}

.rightFlex {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    /* gap: 7vh; */
}

h2 {
    font-size: 10vh;
    width: 100%;
    /* height: 0; */
    padding: 0;
    margin: 0;
}

.blueButton {
    background: #007aff;
    height: 8vh;
    font-size: 3vh;
    color: white;
    outline: none;
    border-radius: 4vh;
    border: none;
    width: 25vw;
    font-family: "Inter";
    display: inline-block;
    transition: 0.3s;
    cursor: pointer;
}

.blueButton:hover {
    background: #2e93ff;
    scale: 1.02;
    transition: 0.3s;
}



@keyframes draw_signature {
  from {
    stroke-dashoffset: 1150;
  }
  to {
    stroke-dashoffset: 0;
  }
}

.cls-1 {
    fill: none;
    stroke: #000;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke-width: 8px;
    stroke-dasharray: 1150;
    stroke-dashoffset: 1150;
    
    /* Link animation to scroll */
    animation: draw_signature linear both;
    animation-timeline: scroll();
    animation-range: 200px 550px; /* Starts at 0px, finishes at 150px scroll */
}