.footerBar {
    position: absolute;
    height: 30vh;
    background-color: black;
    width: 100%;
    overflow-y: none;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 20vh;
}

.leftFooter, .rightFooter {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: row;
    gap: 5vw;
}

.footerLogo {
    color: white;
}

