@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Gravitas+One&family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');

.footerText {
    color: white;
    font-size: 2vh;
    height: 8vh;
    width: 10vh;
    font-family: 'Inter';
    font-weight: 400;
    cursor: pointer;
    transition: 0.2s;
    background-color: black;
    outline: none;
    border: none;
}

.footerText:hover {
    color: #007aff;
    background-color: black;
    transition: 0.2s;
}